import React from 'react';
import { useAuth0 } from '@auth0/auth0-react';

const LogoutButton = () => {
  const { logout, isAuthenticated } = useAuth0();
  if (isAuthenticated) {
    return (
      <button type="button" onClick={() => logout({ returnTo: window.ENV.REACT_APP_AUTH0_CALLBACK_URL })}>
        Log Out
      </button>
    );
  }
  return null;
};

export default LogoutButton;
